  width: 100%;
  height: 40px;
  fill: #999999;
  display: block;
  margin-bottom: 20px;
}

.profile .profile-content .box.is-uploading .box__input, .profile .profile-content .box.is-success .box__input, .profile .profile-content .box.is-error .box__input {
  visibility: hidden;
}

.profile .profile-content .box__uploading, .profile .profile-content .box__success, .profile .profile-content .box__error {
  display: none;
}

.profile .profile-content .box.is-uploading .box__uploading, .profile .profile-content .box.is-success .box__success, .profile .profile-content .box.is-error .box__error {
  display: block;
  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.profile .profile-content .box__uploading {
  text-align: center;
  font-style: italic;
}

.profile .profile-content .box__success {
  text-align: center;
  -webkit-animation: appear-from-inside .25s ease-in-out;
  animation: appear-from-inside .25s ease-in-out;
}

@-webkit-keyframes appear-from-inside {
  from {
    -webkit-transform: translateY(-50%) scale(0);
  }
  75% {
    -webkit-transform: translateY(-50%) scale(1.1);
  }
  to {
    -webkit-transform: translateY(-50%) scale(1);
  }
}

@keyframes appear-from-inside {
  from {
    transform: translateY(-50%) scale(0);
  }
  75% {
    transform: translateY(-50%) scale(1.1);
  }
  to {
    transform: translateY(-50%) scale(1);
  }
}

